
.monofont {
  font-family: monospace;
  font-size: 15px;
}
table,
svg {
  margin: auto;
}
table td {
  vertical-align: top;
}
path,
text {
  fill: black;
}
line,
path,
rect {
  stroke: black;
}
.nonactive path,
.nonactive text {
  fill: gray;
}
.nonactive line,
.nonactive path,
.nonactive rect {
  stroke: gray;
}
.active path,
.active text {
  fill: black;
}
.active line,
.active path,
.active rect {
  stroke: black;
}



